import React, { Fragment } from "react";
import Marquee from "react-fast-marquee";

function TitleInsideBox({
  title,
  theme,
  ticker,
  color,
  fontColor,
  sacle,
  screenMode,
  userName,
}) {
  // transform: scale(0.28349);
  // width: 1245px;
  console.log("fontColor", fontColor);
  console.log("color", color);
  return (
    <Fragment>
      {theme === "Bubble" && title ? (
        <div className="them-box">
          <div
            className={`wraper-them-${theme}  ${ticker && "haveTikcer"}`}
            style={{
              transform: `scale(${sacle})`,
              width: screenMode === "portrait" ? "1248px" : "1248px",
            }}
          >
            {title && !title.ticker && (
              <div className={`${theme} `} style={{ background: color }}>
                {title && !title.ticker && (
                  <text
                    style={{
                      fontSize: userName
                        ? screenMode === "portrait"
                          ? "46px"
                          : "22px"
                        : screenMode === "portrait"
                        ? "55.2px"
                        : "31.2px",
                      color: fontColor,
                    }}
                  >
                    {title}
                  </text>
                )}
              </div>
            )}
          </div>
        </div>
      ) : theme === "Classic" ? (
        <div className="them-box">
          <div
            className={`wraper-them-${theme}  ${ticker && "haveTikcer"}`}
            style={{
              transform: `scale(${sacle})`,
              minWidth: userName
                ? screenMode === "portrait"
                  ? "350px"
                  : "200px"
                : "200px",
              width:
                screenMode === "portrait"
                  ? userName
                    ? "200px"
                    : "1060px"
                  : userName
                  ? "200px"
                  : "1164.8px",
            }}
          >
            <div className="layer" style={{ background: color }}></div>
            <div className="layer2" style={{ background: color }}></div>
            {title && !title.ticker && (
              <div
                className={`${theme} `}
                style={{
                  minHeight: userName ? "50px" : "106px",
                  padding: userName ? "14px" : "6px 60px 6px 50px",
                  width:
                    screenMode === "portrait"
                      ? userName
                        ? "120%"
                        : "100%"
                      : "100%",
                }}
              >
                {title && !title.ticker && (
                  <text
                    style={{
                      fontSize: userName
                        ? screenMode === "portrait"
                          ? "46px"
                          : "22px"
                        : screenMode === "portrait"
                        ? "50.2px"
                        : "31.2px",
                      color: fontColor,
                    }}
                  >
                    {title}
                  </text>
                )}
              </div>
            )}
          </div>
        </div>
      ) : theme === "Minimal" ? (
        <div className="them-box">
          <div
            className={`wraper-them-${theme} ${ticker && "haveTikcer"}`}
            style={{
              transform: `scale(${sacle})`,
              width: screenMode === "portrait" ? "1248px" : "1248px",
            }}
          >
            {title && !title.ticker && (
              <div className="box-Minimal-content">
                <div className="layer"></div>
                <div className={`${theme} `}>
                  {title && !title.ticker && (
                    <text
                      style={{
                        fontSize: userName
                          ? screenMode === "portrait"
                            ? "46px"
                            : "22px"
                          : screenMode === "portrait"
                          ? "51.2px"
                          : "31.2px",
                        color: fontColor,
                      }}
                    >
                      {title}
                    </text>
                  )}
                </div>
              </div>
            )}
            <div className="border-Minimal" style={{ background: color }}></div>
          </div>
        </div>
      ) : theme === "Central" ? (
        <div className="them-box">
          <div
            className={`wraper-them-${theme}  ${ticker && "haveTikcer"}`}
            style={{
              transform: `scale(${sacle})`,
              transformOrigin: userName
                ? screenMode === "portrait"
                  ? "bottom"
                  : "center"
                : "left bottom",
              left: userName ? "0" : "1.25%",
              bottom: userName
                ? screenMode === "portrait"
                  ? "5px"
                  : "0"
                : "2.22222%",
              width:
                screenMode === "portrait"
                  ? userName
                    ? "100%"
                    : "1248px"
                  : userName
                  ? "100%"
                  : "1248px",
            }}
          >
            {title && !title.ticker && (
              <div
                className={`${theme} `}
                style={{
                  background: color,
                  padding: userName ? "12px 14px" : "18px 24px",
                  maxWidth:
                    screenMode === "portrait"
                      ? userName
                        ? "fit-content"
                        : "100%"
                      : screenMode === "portrait"
                      ? userName
                        ? "fit-content"
                        : "100%"
                      : "100%",
                }}
              >
                {title && !title.ticker && (
                  <text
                    style={{
                      fontSize: userName
                        ? screenMode === "portrait"
                          ? "50px"
                          : "22px"
                        : screenMode === "portrait"
                        ? "51.2px"
                        : "31.2px",
                      color: fontColor,
                      minWidth: screenMode === "portrait" ? "550px" : "auto",
                      display: "block",
                      textAlign: "center",
                    }}
                  >
                    {title}
                  </text>
                )}
              </div>
            )}
          </div>
        </div>
      ) : (
        <div className="them-box">
          <div
            className={`wraper-them-${theme} ${ticker && "haveTikcer"}`}
            style={{
              transform: `scale(${sacle})`,
              width:
                screenMode === "portrait"
                  ? userName
                    ? "1248px "
                    : "1248px"
                  : "1248px",
            }}
          >
            {title && !title.ticker && (
              <div className={`${theme} `} style={{ background: color }}>
                {title && (
                  <text
                    style={{
                      fontSize: userName
                        ? screenMode === "portrait"
                          ? "46px"
                          : "22px"
                        : screenMode === "portrait"
                        ? "51.2px"
                        : "31.2px",
                      color: fontColor,
                    }}
                  >
                    {title}
                  </text>
                )}
              </div>
            )}
          </div>
        </div>
      )}
      {ticker && ticker.ticker && (
        <div
          className="ticker-box"
          style={{
            background: color,
            transform: `scale(${sacle})`,
            width: screenMode === "portrait" ? "1275px" : "1275px",
          }}
        >
          <Marquee direction="left">
            <span style={{ color: fontColor }}>{ticker.title}</span>
          </Marquee>
        </div>
      )}
    </Fragment>
  );
}

export default TitleInsideBox;
