import React, { Fragment, useState } from "react";
import "./Viwers.scss";
import { useDispatch } from "react-redux";
import { ManageControlStatus } from "../../store/theme";
import germany from "../../assits/germany.png";
import canada from "../../assits/canada.png";
import france from "../../assits/france.png";
import india from "../../assits/india.png";

import { useSelector } from "react-redux";
import { BoxTooltipTitle } from "../ToolTipsFolder/ToolTips";

function Viwers() {
  let dispatch = useDispatch();
  let ToggleStatus = (e) => {
    dispatch(ManageControlStatus(e));
  };

  let data = [
    {
      numberFlag: "1",
      flag: germany,
      name: "Zaher Hinbarji",
      numberIcon: "12",
      Coins: "10000",
      hertNumber: "3",
    },
    {
      numberFlag: "2",
      flag: canada,
      name: "رامي الطويل اسم طويل جدا جدا جدا جدا جدا",
      numberIcon: "12",
      Coins: "89,485",
      hertNumber: "2",
    },
    {
      numberFlag: "3",
      flag: france,
      name: "George Brown",
      numberIcon: "12",
      Coins: "314",
      hertNumber: "1",
    },
    {
      numberFlag: "50",
      flag: india,
      name: "Ava Evans",
      numberIcon: "19",
      Coins: "2700",
      hertNumber: "2",
    },
    {
      numberFlag: "100",
      flag: france,
      name: "Sophia Clark",
      numberIcon: "12",
      Coins: "1900",
      hertNumber: "4",
    },
    {
      numberFlag: "999",
      flag: canada,
      name: "Lily Wright",
      numberIcon: "12",
      Coins: "1900",
      hertNumber: "0",
    },
    {
      numberFlag: "10000",
      flag: france,
      name: "Evans George",
      numberIcon: "12",
      Coins: "200",
      hertNumber: "0",
    },
  ];

  const [searchTerm, setSearchTerm] = useState("");

  function filterData(data, searchTerm) {
    if (!searchTerm) {
      return data; // Return all data if search term is empty
    }
    return data.filter((viewer) =>
      viewer.name.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }
  const filteredData = filterData(data, searchTerm);

  let { ViewerTapMode } = useSelector((state) => state.themeslice);
  //quiz
  return (
    <div className="wrapper-side">
      <div className="Details  p-2">
        <div className="header">
          <text className="text-acco">Viewers</text>
          <BoxTooltipTitle
            // bigWidth={true}
            // backgroundColor={"rgba(27, 31, 41)"}
            title={
              <div className="" style={{ color: "#fff" }}>
                Viewers
              </div>
            }
            placement="top"
          >
            <i className="fa-regular fa-circle-question ms-2 question" />
          </BoxTooltipTitle>
          <div className="close">
            <i class="fa-solid fa-xmark"></i>
          </div>
        </div>
      </div>

      <div className="BoxViewer">
        {ViewerTapMode === "quiz" && (
          <div className="header-quiz">
            <div className="box-quiz-details">
              <p>
                Correct: 100%. <span>Ponits:250</span>
              </p>
              <p>Last Question:</p>
              <p>
                Participants: 2000,<span>Correct: 10000 = 50%</span>
              </p>
            </div>
          </div>
        )}
        <div className="header">
          <div className="box-inp">
            <input
              type="text"
              placeholder="Search Name"
              onChange={(e) => setSearchTerm(e.target.value.toLowerCase())}
            />
          </div>
        </div>
        <ul className="list-unstyled box-wrapper">
          {filteredData &&
            filteredData.map((e) => {
              return (
                <li>
                  <div className="content">
                    <div className="flag-box">
                      {ViewerTapMode == "quiz" && (
                        <span className="number-flag">{e.numberFlag}</span>
                      )}
                      <div className="wraper-img">
                        <img src={e.flag} alt="flag" />
                      </div>
                    </div>
                    <div className="box-title">
                      <span>{e.name}</span>
                    </div>

                    <div className="details">
                      {ViewerTapMode === "quiz" && (
                        <Fragment>
                          <div className="box-number">
                            <i className="fa-solid fa-check" />
                            <span>{e.numberIcon}</span>
                          </div>

                          <div className="box-coins">
                            <div className="backColor"></div>
                            <span>{e.Coins}</span>
                          </div>

                          <div className="box-heart">
                            <i
                              className="fa-solid fa-heart"
                              style={{
                                color: e.hertNumber == 0 && "gray ",
                              }}
                            >
                              <span>{e.hertNumber}</span>
                            </i>
                          </div>
                        </Fragment>
                      )}
                    </div>
                  </div>
                  <div className="action">
                    <i className="fa-solid fa-ellipsis-vertical" />
                  </div>
                </li>
              );
            })}
        </ul>
      </div>
    </div>
  );
}

export default Viwers;
