import React, { Fragment, useEffect, useRef, useState } from "react";
import "./PollsTap.scss";
import { useDispatch, useSelector } from "react-redux";
import { DragDropContext, Draggable, Droppable } from "react-beautiful-dnd";

import { BoxTooltipTitle } from "../ToolTipsFolder/ToolTips";
import {
  DeletePoll,
  ManageShowBoxCreatePoll,
  ManageShowSelect,
  ShowBoxEditCreateQP,
} from "../../store/theme";

function PollsTap() {
  let { polls, currentSelected } = useSelector((state) => state.themeslice);

  let [data, setData] = useState(polls);
  useEffect(() => {
    setData(polls);
  }, [polls]);
  let dispatch = useDispatch();

  let DeleteFn = (ee) => {
    dispatch(DeletePoll(ee));
  };

  const handleShowPall = (e) => {
    dispatch(ManageShowSelect({ value: e, type: "poll" }));
  };

  let onDragStart = (e) => {};

  const onDragEnd = (result) => {
    if (!result.destination) {
      return;
    }

    const { source, destination } = result;
    if (source.index === destination.index) {
      return; // Item dropped in the same position
    }

    const updatedList = reorder(data, source.index, destination.index);
    setData(updatedList);
  };

  const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);
    return result;
  };

  let HandleAddPall = () => {
    dispatch(ShowBoxEditCreateQP({ true: true, show: "poll" }));
  };

  let HandleEditPoll = (e) => {
    dispatch(ShowBoxEditCreateQP({ value: e, type: "edit", show: "poll" }));
  };

  let checkActive = (id) => {
    if (currentSelected.type === "poll") {
      return currentSelected.value.id === id ? true : false;
    } else {
      return false;
    }
  };

  return (
    <DragDropContext onDragEnd={onDragEnd} onDragStart={onDragStart}>
      <div className="wrapper-side all-action">
        <div className="Details p-2">
          <div className="header">
            <text className="text-acco">Polls</text>
            <BoxTooltipTitle
              // bigWidth={true}
              // backgroundColor={"rgba(27, 31, 41)"}
              title={
                <div className="" style={{ color: "#fff" }}>
                  Polls
                </div>
              }
              placement="top"
            >
              <i className="fa-regular fa-circle-question ms-2 question" />
            </BoxTooltipTitle>
            <div className="close">
              <i class="fa-solid fa-xmark"></i>
            </div>
          </div>

          {/**List Of Banners  Banners */}
          <Droppable droppableId="draggable">
            {(provided, snapshot) => (
              <ul
                className="list-unstyled"
                ref={provided.innerRef}
                {...provided.droppableProps}
              >
                {data &&
                  data.map((value, index) => (
                    <Draggable
                      key={value.id}
                      draggableId={value.id.toString()}
                      index={index}
                    >
                      {(provided, snapshot) => (
                        <li
                          className={`notAdded draggable-item ${
                            snapshot.isDragging ? "dragging" : ""
                          } ${checkActive(value.id) && "active"}`}
                          // style={AddTitleStage === value.id ? style2 : style1}
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                        >
                          <Fragment>
                            <div className="box-title">
                              <div className="box-icons">
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  viewBox="0 0 448 512"
                                >
                                  <path d="M160 80c0-26.5 21.5-48 48-48h32c26.5 0 48 21.5 48 48V432c0 26.5-21.5 48-48 48H208c-26.5 0-48-21.5-48-48V80zM0 272c0-26.5 21.5-48 48-48H80c26.5 0 48 21.5 48 48V432c0 26.5-21.5 48-48 48H48c-26.5 0-48-21.5-48-48V272zM368 96h32c26.5 0 48 21.5 48 48V432c0 26.5-21.5 48-48 48H368c-26.5 0-48-21.5-48-48V144c0-26.5 21.5-48 48-48z" />
                                </svg>
                              </div>
                              <div className="title">
                                {value.element && (
                                  <div className="element">
                                    <img
                                      src={
                                        typeof value.element === "string" ||
                                        value.src === "string"
                                          ? value.src || value.element
                                          : URL.createObjectURL(
                                              value.src || value.element
                                            )
                                      }
                                      alt=".."
                                    />
                                  </div>
                                )}
                                <span>{value.title}</span>
                              </div>
                            </div>

                            <div
                              className="box-action"
                              onClick={(e) => {
                                handleShowPall(value);
                              }}
                            >
                              <div className="Title">
                                {!checkActive(value.id) ? (
                                  <div className="Title">
                                    <i className="fa-solid fa-circle-plus" />
                                    <text>Show</text>
                                  </div>
                                ) : (
                                  <div className="Title">
                                    <i className="fa-solid fa-circle-plus" />
                                    <text>Hide</text>
                                  </div>
                                )}
                              </div>
                              <div className="icon-edit">
                                <BoxTooltipTitle placement="top" title="Edit">
                                  <div
                                    className="edit"
                                    style={{
                                      margin: "0",
                                      marginRight: "-5px",
                                    }}
                                    onClick={(e) => {
                                      e.preventDefault();
                                      e.stopPropagation();
                                      HandleEditPoll(value);
                                    }}
                                  >
                                    <i className="fa-solid fa-pen" />
                                  </div>
                                </BoxTooltipTitle>
                                <BoxTooltipTitle placement="top" title="Delete">
                                  <div
                                    className="edit"
                                    onClick={(e) => DeleteFn(value.id)}
                                  >
                                    <i className="fa-solid fa-trash" />
                                  </div>
                                </BoxTooltipTitle>
                              </div>
                            </div>
                          </Fragment>
                        </li>
                      )}
                    </Draggable>
                  ))}

                <div className="box-create  mb-2">
                  <div
                    className="title-create"
                    onClick={(e) => {
                      HandleAddPall();
                    }}
                  >
                    <span className="plus">+</span>
                    <span>Create a poll</span>
                  </div>
                </div>
              </ul>
            )}
          </Droppable>
        </div>
      </div>
    </DragDropContext>
  );
}

export default PollsTap;
