import React, { Fragment, useState } from "react";
import "./RequestsTap.scss";
import { useDispatch } from "react-redux";
import { ManageControlStatus } from "../../store/theme";
import germany from "../../assits/germany.png";
import canada from "../../assits/canada.png";
import france from "../../assits/france.png";
import india from "../../assits/india.png";
import { useSelector } from "react-redux";
import { BoxTooltipTitle } from "../ToolTipsFolder/ToolTips";

function RequestsTap() {
  let dispatch = useDispatch();
  let ToggleStatus = (e) => {
    dispatch(ManageControlStatus(e));
  };

  let data = [
    {
      numberFlag: "2",
      flag: germany,
      name: "Zaher Hinbarji",
      mic: true,
      cam: false,
    },
    {
      numberFlag: "100",
      flag: canada,
      name: "رامي الطويل اسم طويل جدا جدا جدا جدا جدا",
      mic: false,
      cam: true,
    },
    {
      numberFlag: "2",
      flag: france,
      name: "George Brown",
      mic: true,
      cam: false,
    },
    {
      numberFlag: "100",
      flag: india,
      name: "Ava Evans",
      mic: true,
      cam: false,
    },
    {
      numberFlag: "100",
      flag: france,
      name: "Sophia Clark",
      numberIcon: "12",
      mic: false,
      cam: true,
    },
    {
      numberFlag: "65",
      flag: canada,
      name: "Lily Wright",
      mic: false,
      cam: true,
    },
  ];

  //quiz
  return (
    <div className="wrapper-side ">
      <div className="Details  p-2">
        <div className="header ">
          <text className="text-acco">Join Requests</text>
          <BoxTooltipTitle
            // bigWidth={true}
            // backgroundColor={"rgba(27, 31, 41)"}
            title={
              <div className="" style={{ color: "#fff" }}>
                Join Requests
              </div>
            }
            placement="top"
          >
            <i className="fa-regular fa-circle-question ms-2 question" />
          </BoxTooltipTitle>
          <div className="close">
            <i class="fa-solid fa-xmark"></i>
          </div>
        </div>
      </div>
      <div className="BoxViewer">
        <div className="header">
          <button className="btn btn-primary">
            <text>Choose Random</text>
          </button>
        </div>
        <ul className="list-unstyled box-wrapper">
          {data &&
            data.map((e) => {
              return (
                <li>
                  <div className="content">
                    <div className="flag-box">
                      <div className="wraper-img">
                        <img src={e.flag} alt="flag" />
                      </div>
                    </div>
                    <div className="box-title">
                      <span>{e.name}</span>
                    </div>

                    <div className="details-reuqest">
                      <div className="box-mic">
                        {e.mic ? (
                          <i className="fa-solid fa-microphone" />
                        ) : (
                          <i className="fa-solid fa-video" />
                        )}
                      </div>
                      <div className="box-true">
                        <i className="fa-solid fa-check" />
                      </div>
                      <div className="box-close">
                        <span>x</span>
                      </div>
                    </div>
                  </div>
                  <div className="action">
                    <i className="fa-solid fa-ellipsis-vertical" />
                  </div>
                </li>
              );
            })}
        </ul>
      </div>
    </div>
  );
}

export default RequestsTap;
