import React, { useRef, useState } from "react";
import "./Banners.scss";
import { useDispatch, useSelector } from "react-redux";
import {
  ManageAddBanner,
  ManageAddItemToListAll,
  ManageDeleteBanner,
  ManageEditBanner,
  ManageReorderBanner,
  getTicker,
  getTitleBanner,
} from "../../store/theme";
import { BoxTooltipTitle, TooltipBoxAction } from "../ToolTipsFolder/ToolTips";
import { Dialog } from "@mui/material";
import RenameBanner from "./Modal/RenameBanner";
import DeleteBanner from "./Modal/DeleteBanner";
import { DragDropContext, Draggable, Droppable } from "react-beautiful-dnd";
import { SecurityUpdateWarningSharp } from "@mui/icons-material";
import { randomPassword } from "../../RandomID";

function Banners() {
  let { title, ticker, banners } = useSelector((state) => state.themeslice);
  console.log("title", title);
  console.log("ticker", ticker);
  let [showFolder, setShowFolder] = useState(false);
  let [boxCreate, setBoxCreate] = useState(true);
  let [AddTitleStage, setAddTitleStage] = useState(title ? title.index : "");
  let [AddTickerStage, setAddTickerStage] = useState(
    ticker ? title.ticker : ""
  );
  let [stateEdit, setStateEdit] = useState("");
  let [inp, setInp] = useState("");
  let [tickerValue, setTicker] = useState("");
  let dispatch = useDispatch();
  let handleAdd = (e, index, id, ticker, title) => {
    if (e === "edit") {
      setInp("");
      setTicker("");
      setStateEdit("");
      const values = {
        title: inp ? inp : title,
        ticker: tickerValue === "" ? ticker : tickerValue,
      }; // Create values object
      dispatch(ManageEditBanner({ id, values })); // Dispatch action with id and values
    } else {
      setInp("");
      setTicker("");
      if (inp) {
        dispatch(
          ManageAddBanner({
            id: randomPassword(2),
            title: inp,
            ticker: tickerValue,
            type: "banner",
          })
        );
      }
    }
  };

  let AddToStage = (e, type) => {
    if (type.type === "ticker") {
      console.log("eeeeeeee", e);
      dispatch(getTicker(e));
    } else {
      dispatch(getTitleBanner(e));
    }
  };

  let DeleteFn = (ee) => {
    dispatch(ManageDeleteBanner(ee));
  };

  let style1 = {
    background: "rgb(241, 242, 245)",
    color: "rgb(27, 31, 41)",
  };
  let style2 = {
    background: "rgba(20, 97, 225, 0.9)",
    color: "rgb(255, 255, 255)",
  };

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  let [manage, setManage] = useState("");

  let ManageModal = (e) => {
    setManage(e);
    if (e) {
      handleOpen();
    }
  };

  let [showCreate, setShowCreate] = useState(true);
  let onDragStart = (e) => {
    setShowCreate(false);
  };

  const onDragEnd = (result) => {
    setShowCreate(true);
    if (!result.destination) {
      return;
    }

    const { source, destination } = result;
    if (source.index === destination.index) {
      return; // Item dropped in the same position
    }

    const updatedList = reorder(banners, source.index, destination.index);
    // setData(updatedList);
    dispatch(ManageReorderBanner(updatedList));
  };

  const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);
    return result;
  };
  let checkActive = (ee) => {
    if (ee === ticker.index || ee === title.index || ee === ticker.id) {
      return true;
    } else {
      return false;
    }
  };
  return (
    <DragDropContext onDragEnd={onDragEnd} onDragStart={onDragStart}>
      <div className="wrapper-side all-action">
        <div className=" Details p-2">
          <div className="header">
            <text className="text-acco">Banners</text>
            <BoxTooltipTitle
              bigWidth={true}
              backgroundColor={"rgba(27, 31, 41)"}
              title={<div className="toltip-title-line">Banners</div>}
              placement="top"
            >
              <i className="fa-regular fa-circle-question ms-2 question" />
            </BoxTooltipTitle>
            <div className="close">
              <i class="fa-solid fa-xmark"></i>
            </div>
          </div>

          {/**List Of Banners  Banners */}
          <Droppable droppableId="draggable">
            {(provided, snapshot) => (
              <ul
                className="list-unstyled"
                ref={provided.innerRef}
                {...provided.droppableProps}
              >
                {banners &&
                  banners.map((value, index) => (
                    <Draggable
                      key={value.id}
                      draggableId={value.id.toString()}
                      index={index}
                    >
                      {(provided, snapshot) =>
                        stateEdit === value.id ? (
                          //Edit Banner
                          <div className="box-create  mb-2 mt-2">
                            <div className="box-add">
                              <input
                                type="text"
                                className="form-control"
                                autoFocus
                                placeholder="Enter a banner..."
                                defaultValue={value.title}
                                maxLength={200}
                                onChange={(e) => setInp(e.target.value)}
                              />
                              <p className="count">{inp.length}/200</p>
                              <div className="ticker">
                                <input
                                  id="ticker"
                                  type="checkbox"
                                  className="check-form"
                                  defaultChecked={value.ticker}
                                  onChange={(e) => setTicker(e.target.checked)}
                                />
                                <label htmlFor="ticker">
                                  Scroll across bottom (ticker)
                                </label>
                              </div>

                              <div className="box-btn">
                                <button
                                  className="btn add"
                                  onClick={(e) =>
                                    handleAdd(
                                      "edit",
                                      index,
                                      value.id,
                                      value.ticker,
                                      value.title
                                    )
                                  }
                                >
                                  <text>Save</text>
                                </button>
                                <button
                                  className="btn cancle"
                                  onClick={(e) => {
                                    setStateEdit("");
                                    setInp("");
                                    setBoxCreate(true);
                                  }}
                                >
                                  <text>Cancle</text>
                                </button>
                              </div>
                            </div>
                          </div>
                        ) : (
                          <li
                            className={`${
                              checkActive(value.id) ? "AddToStage" : "notAdded"
                            } draggable-item ${
                              snapshot.isDragging ? "dragging" : ""
                            } ${checkActive(value.id) && "active"}`}
                            style={`${checkActive(value.id) && "active"}`}
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                          >
                            <div className="main-icon">
                              <i className="fa-regular fa-window-maximize" />
                            </div>
                            <div className="box-title">
                              <div
                                className="title"
                                style={{
                                  flexDirection: "column",
                                  alignItems: "flex-start",
                                }}
                              >
                                <span
                                  style={{
                                    WebkitLineClamp: value.ticker ? "1" : "2",
                                  }}
                                >
                                  {value.title}
                                </span>
                                {value.ticker && (
                                  <div className="box-tiker">
                                    <i className="fa-solid fa-left-long" />
                                    <span>ticker</span>
                                    <i className="fa-solid fa-right-long" />
                                  </div>
                                )}
                              </div>
                            </div>
                            <div className="box-action">
                              {checkActive(value.id) ? (
                                <div className="Title">
                                  <div
                                    className="title"
                                    onClick={(e) => {
                                      value.ticker
                                        ? setAddTickerStage("")
                                        : setAddTitleStage("");
                                      AddToStage("", {
                                        type: value.ticker ? "ticker" : "Title",
                                      });
                                    }}
                                  >
                                    <text>Hide</text>
                                  </div>
                                </div>
                              ) : (
                                <div className="Title">
                                  <div
                                    className="Title"
                                    onClick={(e) => {
                                      value.ticker
                                        ? setAddTickerStage(value.id)
                                        : setAddTitleStage(value.id);
                                      AddToStage(
                                        {
                                          title: value.title,
                                          index: value.id,
                                          ticker: value.ticker,
                                        },
                                        {
                                          type: value.ticker
                                            ? "ticker"
                                            : "Title",
                                        }
                                      );
                                    }}
                                  >
                                    <i className="fa-solid fa-circle-plus" />
                                    <text>Show</text>
                                  </div>

                                  <div className="icon-edit">
                                    <BoxTooltipTitle
                                      placement="top"
                                      title="Edit"
                                    >
                                      <div
                                        className="edit"
                                        style={{
                                          margin: "0",
                                          marginRight: "-5px",
                                        }}
                                        onClick={(e) => {
                                          setStateEdit(value.id);
                                          setBoxCreate(true);
                                        }}
                                      >
                                        <i className="fa-solid fa-pen" />
                                      </div>
                                    </BoxTooltipTitle>
                                    <BoxTooltipTitle
                                      placement="top"
                                      title="Delete"
                                    >
                                      <div
                                        className="edit"
                                        onClick={(e) => DeleteFn(value.id)}
                                      >
                                        <i className="fa-solid fa-trash" />
                                      </div>
                                    </BoxTooltipTitle>
                                  </div>
                                </div>
                              )}
                            </div>
                          </li>
                        )
                      }
                    </Draggable>
                  ))}
                {/*Box Create Banner */}
                {showCreate && (
                  <div className="box-create  mb-2">
                    {boxCreate ? (
                      <div
                        className="title-create"
                        onClick={(e) => {
                          setBoxCreate(false);
                        }}
                      >
                        <span className="plus">+</span>
                        <span>Create a banner</span>
                      </div>
                    ) : (
                      <div className="box-add">
                        <input
                          type="text"
                          className="form-control"
                          autoFocus
                          placeholder="Enter a banner..."
                          value={inp}
                          maxLength={200}
                          onChange={(e) => setInp(e.target.value)}
                        />
                        <p className="count">{inp.length}/200</p>
                        <div className="ticker">
                          <input
                            id="ticker"
                            type="checkbox"
                            checked={tickerValue}
                            className="check-form "
                            onChange={(e) => setTicker(e.target.checked)}
                          />
                          <label htmlFor="ticker">
                            Scroll across bottom (ticker)
                          </label>
                        </div>

                        <div className="box-btn">
                          <button
                            className="btn add"
                            onClick={(e) => handleAdd(e)}
                          >
                            <text> Add banner</text>
                          </button>
                          <button
                            className="btn cancle"
                            onClick={(e) => {
                              setBoxCreate(true);
                              setInp("");
                            }}
                          >
                            <text>Cancle</text>
                          </button>
                        </div>
                      </div>
                    )}
                  </div>
                )}
              </ul>
            )}
          </Droppable>
        </div>
      </div>
      <Dialog maxWidth="xs" fullWidth open={open} onClose={handleClose}>
        <div className="modal-box">
          {manage === "Rename" ? <RenameBanner /> : <DeleteBanner />}
        </div>
      </Dialog>
    </DragDropContext>
  );
}

export default Banners;
